*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap');

html,
body,
#root {
  height: 100%;
  scroll-behavior: smooth;
}

header {
  height: 90px;
  background-color: #ffe5bf;
}

main {
  box-sizing: border-box;
  min-height: calc(100vh - 90px);
  padding-bottom: 210px;
}

footer {
  height: 200px;
  margin-top: -200px;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: 'Nunito', sans-serif;
}

h1 {
  font-size: 54px;
  font-weight: 800;
}

h2 {
  font-size: 36px;
  font-weight: 800;
}

h3 {
  font-size: 28px;
  font-weight: 800;
}

h4 {
  font-size: 26px;
  font-weight: 800;
}

h5 {
  font-size: 24px;
  font-weight: 700;
}

a {
  text-decoration: none;
}

footer a {
  color: white;
}

.font_subheading,
.font_button,
.font_body,
.font_subheading_small,
.font_button_small,
.font_body_small,
.font_caption,
.font_caption_small {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
}

.font_subheading,
.font_button,
.font_body {
  font-size: 18px;
}

.font_subheading_small,
.font_button_small,
.font_body_small {
  font-size: 16px;
}

.font_caption,
.font_caption_small {
  font-size: 14px;
}

.content_container {
  padding: 0 135px;
  max-width: 1650px;
  margin: 0 auto;
}

.flex_container {
  display: flex;
  gap: 40px 30px;
  padding-top: 30px;
  flex-wrap: wrap;
  justify-content: space-around;
}

.font_subheading,
.font_subheading_small {
  font-weight: 600;
}

.font_button,
.font_button_small,
.font_caption {
  font-weight: 500;
}

.font_caption_small,
.font_body_small {
  font-weight: 400;
}

@media only screen and (max-width: 1200px) {
  .content_container {
    padding: 0 35px;
  }
}

@media only screen and (max-width: 420px) {
  .content_container {
    padding: 0 15px;
  }
}